.PostPage{
    #back {
        display: inline-block;
        background-color: rgba(30,108,147,1);
        color: white;
        padding: 3px 10px;
        border-radius: 4px;
    }
    
    section {
        width: 50vw;
        margin: 20px auto;
        display: block;
        border: 2px solid rgba(85, 83, 83, 0.4);
        border-radius: 10px;
        padding: 20px;
    }
    h2 {
        font-weight: 400;
        color: rgba(34,110,147,1);
    }
    h1 {
        color: rgba(33,33,33,1);
        font-size: 3vw;
        font-weight: bold;
        width: 50vw;
        margin: 20px auto;
    }
    .addAnswer {
        display: inline-block;
        background-color: rgba(30,108,147,1);
        color: white;
        padding: 3px 10px;
        border-radius: 4px;
        float: right;
        margin-top: 10px;
    }
    textarea {
        width: 100%;
    }
}

